<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-12">
        <div class="card content border-1 shadow-sm mt-3 fixed-card">
          <div class="card-body">
            <div class="row mt-4 mb-4">
              <div class="col-6 content-header">
                <h5>STOCK CONTROL</h5>
              </div>
              <div class="col-6 content-header small">
                <span class="float-right">
                <router-link to="/" class="cursor-pointer" tag="span">
                    dashboard
                </router-link> >
                <router-link to="/inventory/dashboard" class="cursor-pointer" tag="span">
                    inventory
                </router-link>
                 >
                <router-link to="/inventory/stock/control" class="cursor-pointer" tag="span">
                    stock
                </router-link>

                </span>
              </div>
            </div>
            <div class="row">
              <div class="col-5">
                <form class="form-inline">
                  <div class="input-group content-search border">
                    <div class="input-group-prepend">
                      <span class="input-group-text" id="basic-addon1">
                        <i class="fa fa-search"></i>
                      </span>
                    </div>
                    <input
                      type="text"
                      v-model="productId"
                      class="form-control hz-input"
                      placeholder="Search by Product Id"
                      aria-label="Search by Product Id"
                      aria-describedby="basic-addon1"
                      @blur = "fetchItem('productId')"
                    />
                  </div>
                </form>
              </div>
              <div class="col-2 text-center">
                <span>Or</span>
              </div>
              <div class="col-5">
                <div class="float-right">
                  <button class="btn btn-success btn-sm float-right">
                    Scan BarCode
                  </button>
                </div>
              </div>
            </div>
            <hr />
            <!-- content -->
            <div class="row" v-if="item">
              <div class="col-12">
                <!-- item detail -->
                <div class="row">
                  <div class="col-12">
                    <div class="text-center">
                      <span>{{item.name}}</span>
                    </div>
                    <div>
                      <div class="row mt-3">

                        <!-- stock -->
                        <div class="col-6">
                          <span class="font-weight-bold">Available Stock</span>
                          <span>{{item.stock}}</span>
                        </div>
                        <!-- total stock -->
                        <div class="col-6 text-right">
                          <span class="font-weight-bold">Total Stock</span>
                          <span>{{item.totalStock}}</span>
                        </div>
                      </div>
                      <div class="row mt-2">
                        <!-- msp -->
                        <div class="col-4">
                          <span class="font-weight-bold">MSP</span>
                          <span>{{item.msp}}</span>
                        </div>
                        <!-- current price -->
                        <div class="col-4 text-center">
                          <span class="font-weight-bold">Current Price</span>
                          <span>{{item.currentPrice}}</span>
                        </div>
                        <!-- mrp -->
                        <div class="col-4 text-right">
                          <span class="font-weight-bold">MRP</span>
                          <span>{{item.MRP}}</span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <hr />
                <!-- stock -->
                <div v-if="user.designation=='admin' || user.uuid==item.uploadedBy">
                  <div class="row">
                    <div class="col-12 text-center">
                      <div class="mb-2">Amount of Stock:</div>
                      <div>
                        <span class="btn btn-outline-success" @click.prevent="changeStock('inc')">+</span>
                        <span class="pl-1 pr-1">{{newStock}}</span>
                        <span class="btn btn-outline-success" @click.prevent="changeStock('dec')">-</span>
                      </div>
                    </div>
                  </div>

                  <div class="row">
                    <div class="col-12 text-center mt-2">
                      <label class="font-weight-semibold">Inc/Dec</label>
                      <br />
                      <form
                        class="form-inline justify-content-center"
                      >

                        <select
                          v-model="stockChange"
                          name="stockChange"
                          class="custom-select"
                        >
                          <option value="inc">Increase</option>
                          <option value="dec">Decrease</option>
                        </select>
                      </form>
                    </div>
                  </div>
                  <div class="row">
                    <div class="col-12 mt-2">
                      <div class="float-right">
                        <button type="submit" class="btn btn-success btn-sm" @click.prevent="updateStock()">
                          Update
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="row" v-else>
                  <div class="col-12 mt-4">
                    <div class="text-center">
                      <img
                        src="../../../assets/exclamation-mark.svg"
                        class="img-fluid align-self-center mb-2"
                        style="width:5em"
                      />
                      <br />
                      <span>You aren't allowed to update stock</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="row" v-else>
              <div class="col-12 mt-4">
                <div class="text-center">
                  <img
                    src="../../../assets/exclamation-mark.svg"
                    class="img-fluid align-self-center mb-2"
                    style="width:5em"
                  />
                  <br />
                  <span>Search Some Item</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <Loader :disable="disable" :success="success" :message="message" />
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { bus } from "../../../main";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import store from "@/store/store";
import Loader from "../../Loader";
import firebase from "firebase";
import VueBarcodeScanner from 'vue-barcode-scanner'

export default {
  name: "control_stock",
  data() {
    return {
      item: null,
      productId: null,
      barcode: null,
      newStock: 0,
      stockChange: 'inc',
      disable: false,
      success: false,
      message: null,
    };
  },

  components: {
    Loader
  },

  computed: {
    user() {
      return store.state.user;
    },

  },


  methods:{

    changeStock(type){
      if(type=='inc'){
        this.newStock++;
      } else {
        if(this.newStock>0){
          this.newStock--;
        }
      }
    },

    fetchItem(by) {
      let url = domain + "item";
      if(by=='productId' && this.productId.length>=6){
        this.disable = true;
        axios
          .get(url, {
            params: {
              productId: this.productId
            }
          })
          .then(res => {
            if(res.data.length>0){
              res.data.forEach(doc => {
                this.item = doc;
              });
              this.message = "Item Found";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            } else {
              this.message = "Item Not Found";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            }
          })
          .catch(err => {
            this.message = "Some error occured";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          });
      } else if(by=='barcode' && this.barcode){
        this.disable = true;
        axios
          .get(url, {
            params: {
              barcode: this.barcode
            }
          })
          .then(res => {
            if(res.data.length>0){
              res.data.forEach(doc => {
                this.item = doc;
              });
              this.message = "Item Found";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            } else {
              this.message = "Item Not Found";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            }
          })
          .catch(err => {
            this.message = "Some error occured";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          });
      }
    },

    updateStock(){
     if(this.user && this.user.status=="active" && (this.user.designation=='admin' || this.user.uuid==this.item.uploadedBy) && this.newStock>0){
      if(this.stockChange == 'dec'){
        this.newStock = -Math.abs(this.newStock);
      }
      let url = domain + "item/" + this.item.productId;
        axios
         .post(
           url,
           {
             _method: "patch",
             stockChange: this.newStock
           },
           {
             withCredentials: true,
             headers: {
               "Content-Type": ContentType,
               "X-Requested-With": XRequestedWith,
               "X-api-token": firebase.auth().currentUser.uid
             }
           })
          .then(res => {
            console.log(res.data);
            if(res.data == 'Success'){
              this.message = "Data Update Success";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            } else {
              this.message = "Some Error Occured";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            }
          });
     } else {
       if(this.newStock<=0){
         alert("Please enter some stock");
       }
        else if(this.user.status!="active" || this.user.designation!='admin' || this.user.uuid!=this.item.uploadedBy){
          alert("You can't update this item");
        }
     }
    }
  }



};
</script>



