<template>
  <div>
    <!-- adding modal -->
    <!-- <modal
      v-if="showDeleteModal"
      @close="showDeleteModal[index]=false"
      :item="modalData"
      :index="modalIndex"
    ></modal>-->
    <div class="table-scroll inventory-table">
      <table class="table table-bordered table-sm table-hover table-items">
        <thead class="thead-dark">
          <tr style="background-color:#EEEEEE">
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="name"
              class="inventory-th"
            >Name of items</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Main or parent category of items"
              class="inventory-th"
            >Category</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="subcategory"
              class="inventory-th"
            >SubCategory</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Co category is visible in tab activity of app"
              class="inventory-th"
            >Co-category</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Product ID"
              class="inventory-th"
            >Product Id</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Stock depicts the number of particular item in inventory"
              class="inventory-th"
            >Stock</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Quantity"
              class="inventory-th"
            >Qty</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Unit(kg, gram, dozens, etc)"
              class="inventory-th"
            >Unit</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Printed Price of an item in rupees"
              class="inventory-th"
            >₹ MRP</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Current price of items set by us"
              class="inventory-th"
            >Current Price ( ₹ )</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Discount set by us"
              class="inventory-th"
            >Dis(%)</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Image"
              class="inventory-th"
            >Image</th>

            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Uploaded At"
              class="inventory-th"
            >Uploaded At</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Action on items"
              class="inventory-th"
            >Actions</th>
          </tr>
        </thead>
        <tbody v-if="items.length>0 && user" class="table-hover">
          <tr
            v-for="(item, index) in items"
            :key="index"
            @mouseenter="showHideImage(index)"
            @mouseleave="showHideImage(index)"
            :class="getRowClass(index)"
          >
            <td
              style="font-size:12px;"
              v-if="user.designation == 'admin' || user.uuid == item.uploadedBy"
            >
              <span>
                <i class="fas fa-circle text-success" aria-hidden="true" v-if="item.active==1"></i>
                <i class="fas fa-circle text-danger" aria-hidden="true" v-else></i>
                {{item.name}}
              </span>
            </td>
            <td
              class="font-weight-medium"
              v-if="user.designation == 'admin' || user.uuid == item.uploadedBy"
            >{{item.catName}}</td>
            <td
              class="font-weight-medium"
              v-if="user.designation == 'admin' || user.uuid == item.uploadedBy"
            >{{item.subcatName}}</td>
            <td
              class="font-weight-medium"
              v-if="user.designation == 'admin' || user.uuid == item.uploadedBy"
            >{{item.cocatName}}</td>
            <td
              class="font-weight-bold small"
              v-if="user.designation == 'admin' || user.uuid == item.uploadedBy"
            >
              {{item.productId}}
              <i
                class="far fa-2x fa-copy pl-1"
                style="cursor:pointer"
                @click="copyProductId(item.productId)"
              ></i>
              <input type="hidden" :value="item.productId" :id="item.productId" />
            </td>
            <td
              class="font-weight-bold"
              v-if="user.designation == 'admin' || user.uuid == item.uploadedBy"
            >{{item.stock}}</td>
            <td
              class="font-weight-bold"
              v-if="user.designation == 'admin' || user.uuid == item.uploadedBy"
            >{{item.qty}}</td>
            <td
              class="font-weight-normal"
              v-if="user.designation == 'admin' || user.uuid == item.uploadedBy"
            >{{item.unit}}</td>
            <td
              class="font-weight-bold"
              v-if="user.designation == 'admin' || user.uuid == item.uploadedBy"
            >{{item.mrp}}</td>
            <td
              class="font-weight-bold"
              v-if="user.designation == 'admin' || user.uuid == item.uploadedBy"
            >{{item.currentPrice}}</td>
            <td
              class="font-weight-bold"
              v-if="user.designation == 'admin' || user.uuid == item.uploadedBy"
            >{{item.discount}}</td>
            <td class="small" v-if="user.designation == 'admin' || user.uuid == item.uploadedBy">
              <a :href="item.image" v-if="item.image" target="_blank" class="imageName">
                <img :src="item.image" class="img-fluid" v-if="item.showImage==1" />
                <span v-if="item.showImage==0">{{item.imageName}}</span>
              </a>
              <span v-else>No image</span>
            </td>
            <td
              style="font-size:10px;"
              v-if="user.designation == 'admin' || user.uuid == item.uploadedBy"
            >
              <span v-if="user.designation == 'admin'">{{item.uploadedByName}}</span>
              <br />
              {{item.uploadedAt}}
            </td>
            <td v-if="user.designation == 'admin' || user.uuid == item.uploadedBy">
              <span class="d-inline-block">
                <a
                  class="fa fa-trash text-dark"
                  href="#"
                  data-toggle="modal"
                  data-target="#deleteItem"
                  data-placement="bottom"
                  title="delete this item"
                  @click="deleteItem(index)"
                ></a>

                <router-link
                  :to="{name: 'editItem', params: {productId: item.productId}}"
                  tag="a"
                  target="_blank"
                >
                  <i class="fa fa-pencil-alt text-dark"></i>
                </router-link>

                <input
                  v-if="user.designation=='admin'"
                  type="checkbox"
                  id="active"
                  v-model="item.active"
                  @click.prevent="editActiveState(index)"
                  title="This checkbox will change the active state"
                />
              </span>
            </td>
          </tr>
        </tbody>
        <tbody v-else>
          <tr>
            <td colspan="10" class="border-0">No Item to show!</td>
          </tr>
        </tbody>
        <!-- loader -->
      </table>
    </div>

    <Loader :disable="loading" :success="success" :message="message" />
  </div>
</template>
<script>
import axios from "axios";
import Vue from "vue";
import _ from "lodash";
import $ from "jquery";
import db from "../firebaseInit";
import firebase from "firebase";
import { bus } from "../../main";
import { firestorage } from "../firebaseInit";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import store from "@/store/store";
import Loader from "../Loader";

export default {
  props: {
    products: Array,
    disable: Boolean,
    pageSize: {
      type: Number,
      required: false,
      default: 10
    }
  },

  components: {
    Loader
  },

  data() {
    return {
      //showDeleteModal: [],
      //items: this.products,

      // modalData: null,
      // modalIndex: 0

      tdClass: null,
      deleteClass: [],
      editClass: [],
      message: null,
      success: false
    };
  },

  computed: {
    items: {
      get() {
        return this.products;
      },
      set(newValue) {
        this.products = newValue;
      }
    },

    loading: {
      get() {
        return this.disable;
      },
      set(newValue) {
        this.disable = newValue;
      }
    },

    user() {
      return store.state.user;
    }
  },

  created() {
    bus.$on("searchedItems", data => {
      this.setItems(data["items"]);
    });
  },

  mounted() {
    Vue.nextTick(function() {
      $('[data-toggle="tooltip"]').tooltip();
    });
  },

  methods: {
    copyProductId(id) {
      let prodID = "#" + id;
      let idToCopy = document.querySelector(prodID);
      idToCopy.setAttribute("type", "text");
      idToCopy.select();

      try {
        let successful = document.execCommand("copy");
        let message = successful ? "Copied!" : "Unable to Copy!";
        alert(message);
      } catch (err) {
        alert("Whoops! unable to copy");
      }

      idToCopy.setAttribute("type", "hidden");
      window.getSelection().removeAllRanges();
    },

    deleteItem(index) {
      if (
        this.user &&
        (this.user.uuid == this.items[index].uploadedBy ||
          this.user.designation == "admin")
      ) {
        if (confirm("You are about to delete " + this.items[index].name)) {
          this.loading = true;
          let url = domain + "item/" + this.items[index].productId;
          axios
            .post(
              url,
              {
                _method: "delete"
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {
              if (res.data == "done") {
                db.collection("items")
                  .where("productId", "==", this.items[index].productId)
                  .get()
                  .then(query => {
                    query
                      .forEach(doc => {
                        doc.ref.delete();

                        this.message = "Deleted successfully";
                        this.success = true;
                        this.loading = false;

                        setTimeout(() => {
                          this.success = false;
                          this.message = null;
                        }, 2000);
                      })
                      .catch(err => {
                        this.success = true;
                        this.loading = false;

                        setTimeout(() => {
                          this.success = false;
                          this.message = null;
                        }, 2000);
                      });
                    this.items.splice(index, 1);
                  });
              } else {
                alert(res.data);
              }
            });
        }
      } else {
        alert("You are not allowed to delete this");
      }
    },

    editActiveState(index) {
      if (this.user) {
        if (confirm("You are about to change activation of item")) {
          if (
            this.items[index].catActive == "1" &&
            this.items[index].subcatActive == "1" &&
            this.items[index].cocatActive == "1"
          ) {
            this.loading = true;
            let url = domain + "item/" + this.items[index].productId;

            let setActive = 0;
            if (this.items[index].active == 1) {
              setActive = 0;
            } else {
              setActive = 1;
            }

            axios
              .post(
                url,
                {
                  _method: "put",
                  active: setActive
                },
                {
                  withCredentials: true,
                  headers: {
                    "Content-Type": ContentType,
                    "X-Requested-With": XRequestedWith,
                    "X-api-token": firebase.auth().currentUser.uid
                  }
                }
              )
              .then(res => {
                if (res.data) {
                  Vue.set(this.items[index], "active", res.data.active);
                }

                this.message = "Updated successfully";
                this.success = true;
                this.loading = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              })
              .catch(err => {
                this.message = "Some error occured";
                this.success = true;
                this.loading = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              });
          } else {
            if (this.items[index].catActive != "1") {
              alert(
                "It's category is inactive. So, You can't activate the item."
              );
            } else if (this.items[index].subcatActive != "1") {
              alert(
                "It's subcategory is inactive. So, You can't activate the item."
              );
            } else if (this.items[index].cocatActive != "1") {
              alert(
                "It's cocategory is inactive. So, You can't activate the item."
              );
            }

            Vue.set(this.items[index], active, 0);
          }
        }
      }
    },

    getRowClass(index) {
      if (this.items[index].stock < 5) {
        this.deleteClass[index] = "fa fa-trash modal-trigger text-white";
        this.editClass[index] = "fa fa-pencil-alt text-white";
        return "bg-danger text-white";
      } else {
        this.deleteClass[index] = "fa fa-trash modal-trigger text-red";
        this.editClass[index] = "fa fa-pencil-alt text-red";
        return;
      }
    },

    setItems(items) {
      this.items = items;
    },

    showHideImage(index) {
      if (this.items[index].showImage == 0) {
        Vue.set(this.items[index], "showImage", 1);
      } else {
        Vue.set(this.items[index], "showImage", 0);
      }
    },

    updateSize(size) {
      if (size > 0) {
        this.size = size;

        //updating page counter
        let count = Math.ceil(this.products.length / this.size);
        store.dispatch("patchinventoryPageCount", count);
      }
    }
  }
};
</script>

<style scoped>
.loading {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.inventory-table {
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 60px;
  height: 80vh;
}

.inventory-table tbody td {
  font-weight: 400;
}

.footer {
  height: 50px;
}

.inventory-th {
  position: sticky;
  background-color: #000000;
  font-size: 14px;
  opacity: 1;
  font-weight: 500;
  z-index: 4;
}

.imageName {
  text-decoration: none;
  color: inherit;
}

.table-items {
  font-size: small;
}
/*
@media (max-width: 768px) {
  th {
    position: sticky;
    top: 50px;
  }
} */
</style>



