<template>
  <nav
    class="navbar navbar-expand-md sticky-top bg-white border-vh-b justify-content-between"
    v-if="user"
  >
    <div>
      <button
        class="nav-item pl-0 pr-1 navbar-toggler sidebar-toggler"
        role="presentation"
        id="sidebarCollapse"
        data-toggle="collapse"
        data-target="#usersidebar"
        aria-expanded="false"
        aria-controls="navbar"
      >
        <span class="navbar-toggler-icon">
          <i class="fa fa-align-left"></i>
        </span>
      </button>

      <router-link to="/" class="navbar-brand brand pl-2" tag="a">
        <img
          src="../assets/appLogo.svg"
          alt="FasDo"
          style="width:2.5em"
          class="align-self-center pr-2"
        />
        <span>फasDo</span>
        <small style="font-size: 0.475em">Fasdo kiya kya?</small>
      </router-link>
    </div>

    <!-- middle -->
    <div class="ml-auto mr-auto pl-4 d-none d-lg-block dropdown" v-if="isLoggedIn && showSearchBar">
      <div class="input-group">
        <div class="input-group-prepend">
          <span class="input-group-text nav-form-bg" id="basic-addon1">
            <i class="fa fa-search"></i>
          </span>
        </div>
        <input
          type="text"
          class="form-control form-control-sm hz-input nav-form-bg p-4 w-custom"
          placeholder="Search Here..."
          aria-label="Search Here..."
          aria-describedby="basic-addon1"
          v-model="input"
          @focus="goToSearch()"
          @keyup.enter="searching()"
        />
      </div>
    </div>

    <div>
      <button
        class="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarSupportedContent"
        aria-controls="navbarSupportedContent"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon">
          <i class="fa fa-align-right"></i>
        </span>
      </button>
      <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav">
          <li class="nav-item small">
            <div class="d-none d-sm-block d-md-block d-lg-none dropdown" v-if="isLoggedIn && showSearchBar">
              <div class="input-group">
                <div class="input-group-prepend">
                  <span class="input-group-text nav-form-bg" id="basic-addon1">
                    <i class="fa fa-search"></i>
                  </span>
                </div>
                <input
                  type="text"
                  class="form-control form-control-sm hz-input nav-form-bg p-4 w-custom"
                  placeholder="Search Here..."
                  aria-label="Search Here..."
                  aria-describedby="basic-addon1"
                  v-model="input"
                  @focus="goToSearch()"
                  @keyup.enter="searching()"
                />
              </div>
            </div>
          </li>

          <li class="nav-item align-self-center pr-4 pb-1 dropdown">
            <div
              id="navbarDropdown"
              class="unread-notification"
              role="button"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
            >
              <img src="../assets/bell.svg" style="width:17px" />
              <span class="badge badge-danger">{{countUnreadNotification}}</span>
            </div>
            <ul class="dropdown-menu">
              <li class="head text-light bg-dark">
                <div class="row">
                  <div class="col-lg-12 col-sm-12 col-12">
                    <span
                      class="float-right text-light pr-2"
                      @click.prevent="markAsRead()"
                    >Mark all as read</span>
                  </div>
                </div>
              </li>
              <div class="dropdown-content">
                <li
                  class="notification-box"
                  v-for="notification in notifications"
                  :key="notification.id"
                >
                  <router-link
                    :to="{name: 'ViewOrder', params: {orderId: notification.data.orderId}}"
                  >
                    <div class="col-lg-12">
                      <strong
                        class="text-dark"
                        v-if="notification.data.status && notification.data.status==1"
                      >Order Cancelled</strong>
                      <strong
                        class="text-dark"
                        v-else-if="notification.data.status && notification.data.status==2"
                      >Order request return</strong>
                      <strong
                        class="text-dark"
                        v-else-if="notification.data.status && notification.data.status==3"
                      >Order Placed</strong>
                      <strong
                        class="text-dark"
                        v-else-if="notification.data.status && notification.data.status==4"
                      >Order Processing</strong>
                      <strong
                        class="text-dark"
                        v-else-if="notification.data.status && notification.data.status==5"
                      >Order Packed</strong>
                      <strong
                        class="text-dark"
                        v-else-if="notification.data.status && notification.data.status==6"
                      >Order Dispatched</strong>
                      <strong
                        class="text-dark"
                        v-else-if="notification.data.status && notification.data.status==7"
                      >Order Delivered</strong>
                      <strong
                        class="text-dark"
                        v-else-if="notification.data.status && notification.data.status==8"
                      >Order Failed</strong>
                      <strong
                        class="text-dark"
                        v-else-if="notification.data.status && notification.data.status==9"
                      >Order Return Approved</strong>
                      <strong
                        class="text-dark"
                        v-else-if="notification.data.status && notification.data.status==10"
                      >Order Returned</strong>
                      <strong
                        class="text-dark"
                        v-else-if="notification.data.status && notification.data.status=='Items not available'"
                      >Order Availability</strong>
                      <div>{{notification.data.message}}</div>
                      <small
                        class="text-dark float-right"
                      >{{notification.date}}, {{notification.time}}</small>
                    </div>
                  </router-link>
                </li>
              </div>
            </ul>
            <!-- /dropdown-container -->
          </li>
          <li class="nav-item align-self-center pl-2 pr-4 pb-1">
            <button
              class="d-block navbar-toggler sidebar-toggler"
              role="presentation"
              id="sidebarCollapse"
              data-toggle="collapse"
              data-target="#usersettingSidebar"
              aria-expanded="true"
              aria-controls="navbar"
              @click="switchClass"
            >
              <img src="../assets/gear.svg" style="width:17px" />
            </button>
          </li>
          <li class="nav-item small">
            <router-link to="/login" class="nav-link" v-if="!isLoggedIn">
              <i class="fas fa-power-off pr-1"></i>
              <span class="d-none d-lg-block">Login</span>
            </router-link>
          </li>

          <li class="nav-item small" v-if="user">
            <span class="nav-link d-none d-lg-block" v-if="isLoggedIn">Hi,{{user.name}}</span>
          </li>

          <li class="nav-item small">
            <a
              role="button"
              tabindex="0"
              class="nav-link d-flex align-items-center"
              @click.prevent="logout"
              v-if="isLoggedIn"
            >
              <i class="fas fa-power-off pr-1"></i>
              <span class="d-none d-lg-block">Logout</span>
            </a>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>


<script>
import axios from "axios";
import db from "./firebaseInit";
import firebase from "firebase";
import store from "@/store/store";
import {
  domain,
  utcHours,
  utcMins,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import { bus } from "../main";

export default {
  name: "navbar",
  data() {
    return {
      isLoggedIn: false,
      currentUser: false,
      search: [],
      input: null,
      showSearchBar: true,
      notifications: [],
      countUnreadNotification: 0

      // stockLess: 0,
      // outOfStock: 0
    };
  },

  created() {
    if (firebase.auth().currentUser) {
      this.isLoggedIn = true;
    }
  },

  mounted() {
    setTimeout(() => {
      this.getNotifications();
    }, 2000);
  },

  computed: {
    user() {
      return store.state.user;
    }
  },

  watch: {
    $route: function() {
      // Check if given route is true, if it is then hide Nav.
      if (
        this.$route.name == "current_inventory" ||
        this.$route.name == "UploaderItems" ||
        this.$route.name == "addItem" ||
        this.$route.name == "editItem" ||
        this.$route.name == "outofstock" ||
        this.$route.name == "InactiveItems" ||
        this.$route.name == "category" ||
        this.$route.name == "subcategory" ||
        this.$route.name == "cocategory" ||
        this.$route.name == "inventoryBrands" ||
        this.$route.name == "InventoryImages" ||
        this.$route.name == "searchItem"
      ) {
        this.showSearchBar = true;
      } else {
        this.showSearchBar = false;
      }
    }
  },

  methods: {
    logout() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.go({ path: this.$router.path });
        });
    },

    goToSearch() {
      if (this.$route.name != "searchItem") {
        if (confirm("You'll be taken to search result page.")) {
          this.$router.push({ name: "searchItem" });
        }
      }
    },

    getNotifications() {
      if (this.user) {
        this.notifications = [];
        let url = domain + "user/notification";
        axios
          .get(url, {
            headers: {
              "X-api-token": firebase.auth().currentUser.uid
            }
          })
          .then(res => {
            res.data.forEach(doc => {
              let dateTime = new Date(doc.created_at);
              dateTime.setHours(dateTime.getHours() + utcHours);
              dateTime.setMinutes(dateTime.getMinutes() + utcMins);
              let date =
                dateTime.getDate() +
                "/" +
                dateTime.getMonth() +
                "/" +
                dateTime.getFullYear();
              var AmOrPm = dateTime.getHours() >= 12 ? "Pm" : "Am";
              var hours = dateTime.getHours() % 12 || 12;
              let time =
                hours +
                ":" +
                dateTime.getMinutes() +
                ":" +
                dateTime.getSeconds() +
                " " +
                AmOrPm;

              let notify = {
                data: doc.data,
                date: date,
                time: time,
                read_at: doc.read_at
              };
              this.notifications.unshift(notify);
              if (doc.read_at == null) {
                this.countUnreadNotification++;
              }
            });
          });
      }
    },

    markAsRead() {
      let url = domain + "user/notification/" + this.user.uuid;
      axios
        .post(
          url,
          {
            _method: "put",
            markAsRead: 1
          },
          {
            withCredentials: true,
            headers: {
              "Content-Type": ContentType,
              "X-Requested-With": XRequestedWith,
              "X-api-token": firebase.auth().currentUser.uid
            }
          }
        )
        .then(res => {
          if (res.data) {
            console.log(res.data);
            Vue.set(this.items[index], "active", res.data.active);
          }
        });
    },

    searching() {
      if (this.input) {
        var searched = this.input;
        bus.$emit("searchItem", { searched });
      }
    },

    switchClass() {
      bus.$emit("switchClass");
    }
  }
};
</script>

<style scoped>
.brand {
  font-weight: 800;
  color: #004d40;
}

.nav-form-bg {
  background-color: #fbfbfb;
}

.w-custom {
  width: 32rem;
}

.unread-notification {
  position: relative;
  cursor: pointer;
}

.unread-notification span {
  position: absolute;
  top: -6px;
  left: 6px;
}

a {
  text-decoration: none;
  color: #263238;
}

.dropdown-menu {
  top: 60px;
  right: 0px;
  left: unset;
  width: 500px;
  box-shadow: 0px 5px 7px -1px #c1c1c1;
  padding-bottom: 0px;
  padding: 0px;
}
.dropdown-menu:before {
  content: "";
  position: absolute;
  top: -20px;
  right: 12px;
  border: 10px solid #343a40;
  border-color: transparent transparent #343a40 transparent;
}

.dropdown-content {
  height: 500px;
  overflow-y: scroll;
}

.notification-box {
  padding: 1px 1px;
  border-radius: 3%;
}

@media (max-width: 640px) {
  .dropdown-menu {
    top: 50px;
    left: -16px;
    width: 290px;
  }
}

a:hover {
  color: #263238;
}
</style>



